/* *Nav Bar */


.wrapperz{
	background: #171c24;
	margin-top: 10px;
	margin-bottom: 30px;
	width: 100%;
	z-index: 222;
	position: relative;
  }
  .wrapperz nav{
	position: relative;
	display: -webkit-flex;
	display: flex;
	max-width: calc(100% - 200px);
	margin: 0 auto;
	height: 70px;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
  }
  nav .content{
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
  }
  nav .content .links{
	margin-left: 80px;
	display: -webkit-flex;
	display: flex;
  }
  .content .logo img{
	color: #fff;
	left: 10px;
	font-size: 30px;
	font-weight: 600;
  }
  .content .links li{
	list-style: none;
	line-height: 70px;
  }
  .content .links li a,
  .content .links li label{
	color: #fff;
	font-size: 18px;
	font-weight: 500;
	padding: 9px 17px;
	border-radius: 5px;
	transition: all 0.3s ease;
  }
  .content .links li label{
	display: none;
  }
  .content .links li a:hover,
  .content .links li label:hover{
	background: #ec2917;
  }
  .wrapperz .search-icon,
  .wrapperz .menu-icon{
	color: #fff;
	font-size: 18px;
	cursor: pointer;
	line-height: 70px;
	width: 70px;
	text-align: center;
  }
  .wrapperz .menu-icon{
	display: none;
  }
  .wrapperz #show-search:checked ~ .search-icon .AddBackIcon::before{
	content: "X";
  }
  .wrapperz .search-box{
	position: absolute;
	height: 100%;
	max-width: calc(100% - 50px);
	width: 100%;
	opacity: 0;
	pointer-events: none;
	transition: all 0.3s ease;
  }
  .wrapperz #show-search:checked ~ .search-box{
	opacity: 1;
	pointer-events: auto;
  }
  .search-box input{
	width: 100%;
	height: 100%;
	border: none;
	outline: none;
	font-size: 17px;
	color: #fff;
	background: #171c24;
	padding: 0 100px 0 15px;
  }
  .search-box input::-webkit-input-placeholder{
	color: #f2f2f2;
  }
  .search-box input:-ms-input-placeholder{
	color: #f2f2f2;
  }
  .search-box input::placeholder{
	color: #f2f2f2;
  }
  .search-box .go-icon{
	position: absolute;
	right: 10px;
	top: 50%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
	line-height: 60px;
	width: 70px;
	background: #171c24;
	border: none;
	outline: none;
	color: #fff;
	font-size: 20px;
	cursor: pointer;
  }
  .wrapperz input[type="checkbox"]{
	display: none;
  }
  /* Dropdown Menu code start */
  .content .links ul{
	position: absolute;
	background: #171c24;
	top: 80px;
	z-index: -1;
	opacity: 0;
	visibility: hidden;
  }
  .content .links li:hover > ul{
	top: 70px;
	opacity: 1;
	visibility: visible;
	transition: all 0.3s ease;
  }
  .content .links ul li a{
	display: block;
	width: 100%;
	line-height: 30px;
	border-radius: 0px!important;
  }
  .content .links ul ul{
	position: absolute;
	top: 0;
	right: calc(-100% + 8px);
  }
  .content .links ul li{
	position: relative;
  }
  .content .links ul li:hover ul{
	top: 0;
  }
  /* Responsive code start */
  @media screen and (max-width: 1250px){
	.wrapperz nav{
	  max-width: 100%;
	  padding: 0 20px;
	}
	nav .content .links{
	  margin-left: 30px;
	}
	.content .links li a{
	  padding: 8px 13px;
	}
	.wrapperz .search-box{
	  max-width: calc(100% - 100px);
	}
	.wrapperz .search-box input{
	  padding: 0 100px 0 15px;
	}
  }
  @media screen and (max-width: 900px){
	.wrapperz .menu-icon{
	  display: block;
	}
	.wrapperz #show-menu:checked ~ .menu-icon i::before{
	  content: "\f00d";
	}
	nav .content .links{
	  display: block;
	  position: fixed;
	  background: #14181f;
	  height: 100%;
	  width: 100%;
	  top: 70px;
	  left: -100%;
	  margin-left: 0;
	  max-width: 350px;
	  overflow-y: auto;
	  padding-bottom: 100px;
	  transition: all 0.3s ease;
	}
	nav #show-menu:checked ~ .content .links{
	  left: 0%;
	}
	.content .links li{
	  margin: 15px 20px;
	}
	.content .links li a,
	.content .links li label{
	  line-height: 40px;
	  font-size: 20px;
	  display: block;
	  padding: 8px 18px;
	  cursor: pointer;
	}
	.content .links li a.desktop-link{
	  display: none;
	}
	/* dropdown responsive code start */
	.content .links ul,
	.content .links ul ul{
	  position: static;
	  opacity: 1;
	  visibility: visible;
	  background: none;
	  max-height: 0px;
	  overflow: hidden;
	}
	.content .links #show-features:checked ~ ul,
	.content .links #show-services:checked ~ ul,
	.content .links #show-items:checked ~ ul{
	  max-height: 100vh;
	}
	.content .links ul li{
	  margin: 7px 20px;
	}
	.content .links ul li a{
	  font-size: 18px;
	  line-height: 30px;
	  border-radius: 5px!important;
	}
  }
  @media screen and (max-width: 400px){
	.wrapperz nav{
	  padding: 0 10px;
	}
	.content .logo a{
	  font-size: 27px;
	}
	.wrapperz .search-box{
	  max-width: calc(100% - 70px);
	}
	.wrapperz .search-box .go-icon{
	  width: 30px;
	  right: 0;
	}
	.wrapper .search-box input{
	  padding-right: 30px;
	}
  }
 


 
